var apiToken = 'CijdTZuyBVDePURSejQHGgfMsTjgfJSxarjJkWBo';
var axios = require('axios');

class Discogs {

    constructor() {
        this.titleToLabels = {};
        this.nameToLabelInfo = {};
        this.nameToLabel = {};
        this.idToReleases = {};
    }

    getLabels(title) {
        return new Promise((resolve, reject) => {
            if (this.titleToLabels[title] !== undefined) {
                resolve(this.titleToLabels[title]);
                return;
            }

            this.searchYoutubeTitle(title)
                .then((result) => {
                // get all the labels
                var data = [];
                if (result === null || result.label === undefined) {
                    resolve(data);
                }
                result.label.map((label) => this.getLabelInfo(label, (ret) => {
                    if (ret === null) {
                        resolve(data);
                        return;
                    }
                    var labelData = {
                        id: ret.id,
                        name: ret.name};
                    if ('images' in ret) {
                        labelData['thumbnail'] = ret.images[0].uri;
                    }
                    data.push(labelData);

                    this.titleToLabels[title] = data;
                    if (data.length == result.label.length) {
                        resolve(data);
                    } else {
                    }
                }));
                })
                .catch(err => {
                    reject(err);
                });
        });
    }

    getReleaseInfo(discogsId) {
        return new Promise((resolve, reject) => {
            var url = 'https://api.discogs.com/releases/' + discogsId;
        axios.get(url)
            .then((ret) => {
                if (ret.data !== undefined) {
                    var results = ret.data;
                    resolve(results);
                } else {
                    resolve(undefined);
                }
            });
        });
    }

    searchRecords(recordName) {
        return new Promise((resolve, reject) => {
            console.log("searchinng name=", recordName);
            let query = recordName.split(/\s/g).join('+');
            var url = 'https://api.discogs.com/database/search?q=' + query + '&type=release&token=' + apiToken;
        axios.get(url)
            .then((ret) => {
                if (ret.data.results !== undefined) {
                    var results = ret.data.results;
                    resolve(results);
                } else {
                    resolve([]);
                }
            });
        });
    }

    searchLabel(labelName, callback) {
        if (this.nameToLabel[labelName] !== undefined) {
            return callback(this.nameToLabel[labelName]);
        }
        var query = labelName;
        query = query.split(/\s/g).join('+');
        var url = 'https://api.discogs.com/database/search?q=' + query + '&type=label&token=' + apiToken;
        axios.get(url)
            .then((ret) => {
                if (ret.data.results !== undefined) {
                    var result = ret.data.results[0];
                    this.nameToLabel[labelName] = result;
                    callback(result);
                }
            });

    }

    getLabelInfo(labelName, callback) {
        this.searchLabel(labelName, function (result) {
            var labelId = result.id
            var url = 'https://api.discogs.com/labels/' + labelId + '?token=' + apiToken;
            axios.get(url)
                .then(function (ret) {
                    callback(ret.data);
                })
                .catch(function (err) {
                    callback(null);
                });
        });
            
    }

    getLabelReleases(labelId, page) {
        return new Promise((resolve, reject) => {
            if (this.idToReleases[labelId + '_' + page] !== undefined) {
                resolve(this.idToReleases[labelId + '_' + page]);
                return;
            }
            var url = 'https://api.discogs.com/labels/' + labelId +
            '/releases?token=' + apiToken + '&page=' + page + '&per_page=8';
            axios.get(url)
                .then((ret) => {
                    this.idToReleases[labelId + '_' + page] = ret.data;
                    resolve(ret.data);
                })
                .catch(function (err) {
                    reject(err);
            });
        });
    }

    searchYoutubeTitle(youtubeTitle) {
        return new Promise((resolve, reject) => {
            var searchObject = parseTitleString(youtubeTitle);
            var query = searchObject.artist + ' - ' + searchObject.title;
            query = query.split(/\s/g).join('+');
            var url = 'https://api.discogs.com/database/search?q=' + query + '&token=' + apiToken;
            axios.get(url)
                .then(function (ret) {
                    var results = ret.data.results;
                    var earliestResult = null;
                    for (var i in results) {
                        if ((earliestResult == null ||
                             (results[i].year < earliestResult.year)) && 
                            results[i].title.includes(searchObject.title))
                        {
                            earliestResult = results[i];
                        }
                    }
                    if (earliestResult == null) {
                        // get the first result
                        for (var i in results) {
                            return resolve(results[i]);
                        }
                    }
                    resolve(earliestResult);
                })
                .catch(function (err) {
                    reject(err);
                });;
        });
    }
}

